/**
 * A composable function that displays the top app bar with various elements and options.
 *
 * @param modifier The modifier to be applied to the TopBar component.
 * @param title The title to be displayed in the top app bar.
 * @param isScrolled Whether the content is scrolled.
 * @param shouldShowBackArrowIcon Whether to show the back arrow icon.
 * @param shouldShowAll Whether to show the "All" button.
 * @param shouldShowAllTitle Whether to show the title of the "All" button.
 * @param shouldShowChromeCastIcon Whether to show the ChromeCast icon.
 * @param chromecastIcon platform Chromecast icon.
 * @param onClickAllAction Callback when the "All" button is clicked.
 * @param onNavigateBackIconClickAction Callback when the navigation icon is clicked.
 */
@Suppress("LongMethod")
@Composable
fun TopBar(
    modifier: Modifier = Modifier,
    title: String = "",
    isScrolled: Boolean,
    shouldShowBackArrowIcon: Boolean = true,
    shouldShowAll: Boolean = false,
    shouldShowAllTitle: Boolean = false,
    shouldShowChromeCastIcon: Boolean = false,
    chromecastIcon: @Composable () -> Unit,
    onNavigateBackIconClickAction: () -> Unit,
    onClickAllAction: () -> Unit = { },
    focusRequester: FocusRequester,
    focusManager: FocusManager? = null,
) {
    var shouldDisableSolidBackground by remember { mutableStateOf(true) }
    val background = if (isScrolled && shouldDisableSolidBackground) {
        Brush.verticalGradient(
            colors = listOf(
                MaterialTheme.colorScheme.onPrimary.copy(alpha = 0.9f),
                MaterialTheme.colorScheme.onPrimary.copy(alpha = 0.9f)
            )
        )
    } else {
        Brush.verticalGradient(
            colors = listOf(MaterialTheme.colorScheme.onPrimary, Color.Transparent)
        )
    }
    TopAppBar(
        title = {
            Text(
                text = title,
                style = MaterialTheme.typography.headlineSmall,
                maxLines = 1,
                overflow = TextOverflow.Ellipsis
            )
        },
        modifier = modifier
            .background(background)
            .clickable {
                shouldDisableSolidBackground = !shouldDisableSolidBackground
            },
        colors = topAppBarColors(
            containerColor = Color.Transparent,
            navigationIconContentColor = MaterialTheme.colorScheme.onSecondary,
            titleContentColor = MaterialTheme.colorScheme.onSecondary,
            actionIconContentColor = MaterialTheme.colorScheme.onSecondary
        ),
        navigationIcon = {
            NavigationIcon(
                shouldShowBackArrowIcon = shouldShowBackArrowIcon,
                onClick = onNavigateBackIconClickAction,
                modifier = Modifier
                    .focusRequester(focusRequester)
                    .setKeyEventListener(focusManager)
            )
        },
        actions = {
            Row(verticalAlignment = Alignment.CenterVertically) {
                if (shouldShowAll) {
                    ExpandButton(
                        title = if (shouldShowAllTitle) "Wszystko" else "",
                        iconId = Res.Images.EXPAND,
                        onClickAll = onClickAllAction
                    )
                }
                if (shouldShowChromeCastIcon) {
                    chromecastIcon()
                }
            }
        }
    )
    LaunchedEffect(focusRequester) {
        focusRequester.requestFocus()
    }
}

/**
 * A composable function that displays the navigation icon.
 *
 * @param shouldShowBackArrowIcon Whether to show the back arrow icon.
 * @param onClick Callback when the icon is clicked.
 */
@Composable
private fun NavigationIcon(
    modifier: Modifier = Modifier,
    shouldShowBackArrowIcon: Boolean,
    onClick: () -> Unit
) {
    IconButton(
        modifier = modifier,
        onClick = { onClick() }
    ) {
        if (shouldShowBackArrowIcon) {
            Icon(
                painter = painterResource(DrawableResource(Res.Images.BACK)),
                contentDescription = null,
                modifier = Modifier.rtlIconSupport()
            )
        } else {
            Image(
                painter = painterResource(DrawableResource(Res.Images.LAUNCHER_FOREGROUND)),
                contentDescription = null
            )
        }
    }
}

/**
 * A composable function that displays the "All" button.
 *
 * @param title The title of the button.
 * @param iconId The ID of the button's icon.
 * @param onClickAll Callback when the button is clicked.
 */
@Composable
private fun ExpandButton(title: String, iconId: String, onClickAll: () -> Unit) {
    Card(
        shape = RoundedCornerShape(MultiplatformDimens.small),
        onClick = { onClickAll() },
        colors = CardDefaults.cardColors(containerColor = Color.Transparent)
    ) {
        Row(
            modifier = Modifier.padding(MultiplatformDimens.small),
            horizontalArrangement = Arrangement.spacedBy(MultiplatformDimens.small),
            verticalAlignment = Alignment.CenterVertically
        ) {
            Text(
                modifier = Modifier.padding(start = MultiplatformDimens.xxs),
                text = title,
                style = MaterialTheme.typography.bodyMedium
            )
            Icon(
                painter = painterResource(DrawableResource(iconId)),
                contentDescription = null
            )
        }
    }
}

fun Modifier.setKeyEventListener(
    focusManager: FocusManager?
) =
    onKeyEvent {
        if(it.type == KeyEventType.KeyUp){
            return@onKeyEvent false
        }

        when(it.key){
            Key.DirectionUp -> {
                focusManager?.moveFocus(FocusDirection.Up)
                true
            }
            Key.DirectionDown -> {
                focusManager?.moveFocus(FocusDirection.Down)
                true
            }
            Key.DirectionLeft -> {
                focusManager?.moveFocus(FocusDirection.Left)
                true
            }
            Key.DirectionRight -> {
                focusManager?.moveFocus(FocusDirection.Right)
                true
            }
            else -> false
        }
    }