package co.proexe.blueonline.ui.multiplatform.theme

import androidx.compose.material3.ColorScheme
import androidx.compose.material3.darkColorScheme
import androidx.compose.material3.lightColorScheme
import androidx.compose.ui.graphics.Color
import co.proexe.core.ui.multiplatform.BlueonlineColorsConfig

/**
 * This file contains color configuration for the Blueonline UI theme.
 *
 * The [Color] object provides color schemes for both light and dark themes, along with
 * various color properties used throughout the UI.
 */
object Color {

    val AppLightColorScheme by lazy {
        lightColorScheme(
            primary = cachedPrimaryColor,
            onPrimary = cachedWhiteColor,
            secondary = cachedSecondaryColor,
            onSecondary = cachedBlackColor,
            error = cachedErrorColor,
        )
    }
    val AppDarkColorScheme by lazy {
        darkColorScheme(
            primary = cachedDarkPrimaryColor,
            onPrimary = cachedDarkWhiteColor,
            secondary = cachedDarkSecondaryColor,
            onSecondary = cachedDarkBlackColor,
            error = cachedDarkErrorColor,
            background = cachedBlackColor,
        )
    }

    val ColorScheme.grey
        get() = if (isDarkTheme) cachedDarkGreyDarkColor else cachedDarkGreyColor
    val ColorScheme.darkGrey
        get() = if (isDarkTheme) cachedGreyDarkColor else cachedGreyColor
    val ColorScheme.success
        get() = if (isDarkTheme) cachedDarkSuccessColor else cachedSuccessColor
    val ColorScheme.disabledButton
        get() = if (isDarkTheme) cachedDarkDisabledButton else cachedDisabledButton

    // Light Theme
    val cachedPrimaryColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.primaryLight))
    val cachedSecondaryColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.secondaryLight))
    val cachedErrorColor = Color(AndroidColor.parseColor(BlueonlineColorsConfig.errorLight))
    val cachedWhiteColor = Color(AndroidColor.parseColor(BlueonlineColorsConfig.whiteLight))
    val cachedBlackColor = Color(AndroidColor.parseColor(BlueonlineColorsConfig.blackLight))
    val cachedGreyColor = Color(AndroidColor.parseColor(BlueonlineColorsConfig.greyLight))
    val cachedGreyDarkColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.darkGreyLight))
    val cachedSuccessColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.successLight))
    val cachedDisabledButton =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.disabledButtonLight))

    // Dark Theme
    val cachedDarkPrimaryColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.primaryDark))
    val cachedDarkSecondaryColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.secondaryDark))
    val cachedDarkErrorColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.errorDark))
    val cachedDarkWhiteColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.whiteDark))
    val cachedDarkBlackColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.blackDark))
    val cachedDarkGreyColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.greyDark))
    val cachedDarkGreyDarkColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.darkGreyDark))
    val cachedDarkSuccessColor =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.successDark))
    val cachedDarkDisabledButton =
        Color(AndroidColor.parseColor(BlueonlineColorsConfig.disabledButtonDark))

    private var isDarkTheme = false

    /**
     * Generates a [ColorScheme] based on the provided theme mode.
     *
     * @param isDarkTheme Boolean indicating whether the theme is in dark mode.
     * @return [ColorScheme] instance representing the appropriate color scheme.
     */
    fun colorScheme(isDarkTheme: Boolean) = if (isDarkTheme) {
        co.proexe.blueonline.ui.multiplatform.theme.Color.isDarkTheme = true
        AppDarkColorScheme
    } else {
        co.proexe.blueonline.ui.multiplatform.theme.Color.isDarkTheme = false
        AppLightColorScheme
    }
}

object AndroidColor {
    @Suppress("MagicNumber", "UseRequire")
    fun parseColor(colorString: String): Int {
        // Use a long to avoid rollovers on #ffXXXXXX
        var color: Long = colorString.substring(1).toLong(16)
        if (colorString.length == 7) {
            // Set the alpha value
            color = color or 0x00000000ff000000L
        } else if (colorString.length != 9) {
            throw IllegalArgumentException("Unknown color")
        }
        return color.toInt()
    }
}
