@Composable
fun Section(
    title: String,
    focusManager: FocusManager,
    focusRequester: FocusRequester,
    navigateToProductDetails: () -> Unit,
) {
    Column(
        verticalArrangement = Arrangement.spacedBy(12.dp)
    ) {
        Text(title)
        LazyRow(
            horizontalArrangement = Arrangement.spacedBy(8.dp)
        ) {
            items(10) {
                SectionTile(
                    focusRequester = if (it == 0) focusRequester else FocusRequester.Default,
                    focusManager = focusManager,
                    navigateToProductDetails = navigateToProductDetails,
                    imageUrl = generateRandomImageUrl()
                )
            }
        }
    }
}

@Composable
private fun SectionTile(
    focusRequester: FocusRequester,
    focusManager: FocusManager,
    navigateToProductDetails: () -> Unit,
    imageUrl: String,
) {
    var alpha = remember { mutableStateOf(0.3f) }

    Surface(modifier = Modifier
        .width(200.dp)
        .height(120.dp)
        .clip(RoundedCornerShape(15.dp))
        .background(MaterialTheme.colorScheme.onSecondary.copy(alpha = alpha.value))
        .focusRequester(focusRequester)
        .setKeyEventListener(focusManager)
        .onFocusChanged {
            if (it.isFocused) {
                alpha.value = 0.2f
            } else {
                alpha.value = 0.3f
            }
        },
        onClick = { navigateToProductDetails() }
    ) {
        val size = mutableStateOf(0.9f)

        val painterResource: Resource<Painter> = asyncPainterResource(
            imageUrl,
            filterQuality = FilterQuality.High,
        )

        KamelImage(
            resource = painterResource,
            contentDescription = null,
            modifier = Modifier
                .fillMaxSize(size.value)
                .onFocusChanged {
                    if (it.isFocused) {
                               size.value = 1f
                    }else{
                        size.value = 0.9f
                    }
                },
            contentScale = ContentScale.FillBounds,
            onLoading = {
                CircularProgressIndicator(
                    progress = { it },
                )
            },
            onFailure = { exception: Throwable ->
                console.log(
                    "No trudno :( -> ${exception.message}"
                )
            },
        )
    }
}