import moe.tlaster.precompose.navigation.Navigator
import moe.tlaster.precompose.navigation.RouteBuilder

private const val SECTION_SCREEN_ROUTE = "/sections"

fun Navigator.navigateToSectionScreen() = navigate(SECTION_SCREEN_ROUTE)

fun RouteBuilder.homeScreenRoute(
    focusManager: FocusManager,
    focusRequester: FocusRequester,
    navigateToProductDetails: () -> Unit
) {
    scene(SECTION_SCREEN_ROUTE) {
        SectionScreenContent(
            focusManager,
            focusRequester,
            navigateToProductDetails
        )
    }
}

@Composable
fun SectionScreenContent(
    focusManager: FocusManager,
    focusRequester: FocusRequester,
    navigateToProductDetails: () -> Unit
) {
    val listState = rememberLazyListState()

    Box(
        modifier = Modifier
            .fillMaxSize()
            .background(MaterialTheme.colorScheme.onPrimary)
            .focusRequester(focusRequester)
    ) {
        LazyColumn(
            verticalArrangement = Arrangement.spacedBy(MultiplatformDimens.small),
            contentPadding =
            PaddingValues(
                bottom = MultiplatformDimens.extraLarge
            ),
            state = listState
        ) {
            item {
                TopBar(
                    modifier = Modifier
                        .setKeyEventListener(focusManager),
                    title = "Experimental web",
                    isScrolled = false,
                    shouldShowBackArrowIcon = true,
                    shouldShowChromeCastIcon = true,
                    onNavigateBackIconClickAction = { },
                    chromecastIcon = {},
                    focusRequester = focusRequester,
                    focusManager = focusManager
                )
            }
            items(10) {
                Section(
                    title = "Sekcja $it",
                    focusManager = focusManager,
                    focusRequester = FocusRequester.Default,
                    navigateToProductDetails,
                )
            }

        }
    }
}