package co.proexe.blueonline.web.navigation

import androidx.compose.foundation.MutatePriority
import androidx.compose.runtime.Composable
import androidx.compose.runtime.CompositionLocalProvider
import androidx.compose.runtime.remember
import androidx.compose.ui.focus.FocusManager
import androidx.compose.ui.focus.FocusRequester
import androidx.compose.ui.platform.LocalFocusManager
import co.proexe.blueonline.ui.multiplatform.theme.BlueOnlineTheme
import co.proexe.blueonline.web.typography2
import co.proexe.blueonline.web.ui.section.homeScreenRoute
import co.proexe.blueonline.web.ui.section.navigateToProductDetails
import co.proexe.blueonline.web.ui.section.productDetailsRoute
import io.kamel.core.config.Core
import io.kamel.core.config.KamelConfig
import io.kamel.core.config.takeFrom
import io.kamel.image.KamelImage
import io.kamel.image.config.Default
import io.kamel.image.config.LocalKamelConfig
import moe.tlaster.precompose.PreComposeApp
import moe.tlaster.precompose.navigation.NavHost
import moe.tlaster.precompose.navigation.rememberNavigator

@Composable
fun AppNavigation(focusManager: FocusManager, focusRequester: FocusRequester) {
    val kamelConfig = KamelConfig.Default
    CompositionLocalProvider(LocalKamelConfig provides kamelConfig){
        BlueOnlineTheme(
            isDarkTheme = true,
            typography = typography2
        ) {
            PreComposeApp {
                val navigator = rememberNavigator()
                NavHost(
                    navigator = navigator,
                    initialRoute = "/sections"
                ) {
                    homeScreenRoute(
                        navigateToProductDetails = navigator::navigateToProductDetails,
                        focusManager = focusManager,
                        focusRequester = focusRequester
                    )
                    productDetailsRoute(
                        navigateBackAction = {
                            navigator.goBack()
                        },
                        focusManager = focusManager,
                        focusRequester = focusRequester
                    )
                }
            }
        }
    }

}
