package co.proexe.blueonline.web.ui

import androidx.compose.foundation.background
import androidx.compose.material3.Scaffold
import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.focus.FocusRequester
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.platform.LocalFocusManager
import co.proexe.blueonline.ui.multiplatform.theme.BlueOnlineTheme
import co.proexe.blueonline.web.navigation.AppNavigation
import co.proexe.blueonline.web.typography2

@Composable
fun AppScreen() {
    BlueOnlineTheme(
        isDarkTheme = true,
        typography = typography2
    ) {
        val focusManager = LocalFocusManager.current
        val focusRequester = remember { FocusRequester() }

        Scaffold(
            modifier = Modifier.background(Color.Black),
        ) {
            AppNavigation(
                focusManager,
                focusRequester
            )
        }
    }
}