package co.proexe.blueonline.web.ui.section

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.focus.FocusManager
import androidx.compose.ui.focus.FocusRequester
import co.proexe.blueonline.ui.multiplatform.components.topNavBar.TopBar
import co.proexe.blueonline.web.app
import co.proexe.blueonline.web.util.setKeyEventListener
import moe.tlaster.precompose.navigation.Navigator
import moe.tlaster.precompose.navigation.RouteBuilder

private const val PRODUCT_DETAILS_ROUTE = "/details"

fun Navigator.navigateToProductDetails() = navigate(PRODUCT_DETAILS_ROUTE)

fun RouteBuilder.productDetailsRoute(
    focusManager: FocusManager,
    focusRequester: FocusRequester,
    navigateBackAction: () -> Unit
) {
    scene(PRODUCT_DETAILS_ROUTE) {
        ProductDetailsContent(
            focusManager,
            focusRequester,
            navigateBackAction
        )
    }
}

@Composable
fun ProductDetailsContent(
    focusManager: FocusManager,
    focusRequester: FocusRequester,
    navigateBackAction: () -> Unit
) {
    Column(
        modifier = Modifier.fillMaxSize().background(MaterialTheme.colorScheme.background)
    ) {
        TopBar(
            modifier = Modifier
                .setKeyEventListener(focusManager),
            title = "Detal sekcji",
            isScrolled = false,
            shouldShowBackArrowIcon = true,
            shouldShowChromeCastIcon = true,
            chromecastIcon = {},
            onNavigateBackIconClickAction = { navigateBackAction() },
            focusRequester = focusRequester,
            focusManager = focusManager
        )
//        app()
    }
}