package co.proexe.blueonline.ui.multiplatform.theme

import androidx.compose.foundation.isSystemInDarkTheme
import androidx.compose.material3.MaterialTheme
import androidx.compose.material3.Shapes
import androidx.compose.material3.Typography
import androidx.compose.runtime.Composable
import androidx.compose.runtime.CompositionLocalProvider

/**
 * Composable function that applies the BlueOnline theme to the content.
 *
 * @param isDarkTheme Boolean indicating whether the theme should be in dark mode.
 * @param content The content to which the theme should be applied.
 */
@Composable
fun BlueOnlineTheme(
    isDarkTheme: Boolean = isSystemInDarkTheme(),
    typography: Typography = typography1,
    content: @Composable () -> Unit
) {
    CompositionLocalProvider(LocalAppDimensions provides phoneDimens) {
        MaterialTheme(
            colorScheme = Color.colorScheme(isDarkTheme),
            typography = typography,
            shapes = Shapes(),
            content = content
        )
    }
}
